*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

/*Start of all styling done on the index.html*/
#introVisual {
    position: fixed;
    right: 175px;
    bottom: 300px;
    z-index: -2;
    width: 100%;
    height: 500px;
}

.welcomeText{
    display: flex;
    justify-content: center;
    align-items: center;

}

.welcomeText h1{
    font-size: 150px;
    color: red;
    opacity: 0.5;
}

.welcomeText h2{
    font-size: 25px;
    color: blueviolet;
    
}

/*end of index.html styling */

/* Start Styling for Navigation*/
/*start of styling for index navigation; different from main nav*/
#navIcon{
    /* height: 10vh; */
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 30px;
    text-decoration: none;
}

#navLinks{
    text-decoration: none;
    font-size: 35px;
}

#introLink{
    display: flex;
    border: 2px solid grey;
    border-radius: 35px;
    background-color: rgba(29, 28, 28, 0.844);
}
/*end of styling for index navigation*/


.menuLogo{
    border-radius: 30px;
}

.homeButton{
    color: azure;
}


/* End of Styling for Navigation*/

/*Start of styling for home.html*/


.home{
    /* height: 90vh; */
    position: absolute;
    width: 100%;
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}



/*End of styling for home.html*/
